export default {
    name: "bd-flash-sale",
    props: {
        flashSale: {
            type: Object,
            default() {
                return {
                    time_status: 1,
                    start_at: '',
                    end_at: '',
                    min_discount: ''
                }
            }
        },
        theme: Object
    },
    computed:{
        flashStyle() {
            if(this.theme.theme == 'a' && this.flashSale.cover) {
                return "background-image: url('" + this.flashSale.cover + ')'
            }else {
                return "background:" + this.theme.background_p
            }
        }
    },
    methods: {
        navigator() {
            uni.navigateTo({
                url: this.flashSale.url
            });
        }
    }
}
